<template>
  <div class="main-div">
    <div class="tag">
      <svg :width="width" :height="height" @mousemove="listener($event)">
        <a :key="index" rel="external nofollow" v-for="(tag,index) in tags">
          <text
            :x="tag.x"
            :y="tag.y"
            :font-size="20 * (600/(600-tag.z))"
            :fill-opacity="((400+tag.z)/600)"
            @click="s(tag.text)"
          >{{tag.text}}</text>
        </a>
      </svg>
    </div>
    <div class="input-search">
      <el-input placeholder="" v-model="input" class="input-with-select">
        <!-- <el-select slot="prepend" placeholder="" v-model="choose" @click="getItem(item.value)">
          <el-option
            :label="item.label"
            v-for="(item) in choose"
            :key="item.value"
            :value="item.value"
          ></el-option>
          <el-option label="ID" value="2"></el-option>
          <el-option label="" value="3"></el-option>
        </el-select> -->
        <el-button slot="append" icon="el-icon-search" @click="search()"></el-button>
      </el-input>
    </div>

    <div class="sku-box store-content">
      <div class="sort-option">
        <ul class="line clear">
          <li>
            <a href="javascript:;" :class="{'active':checked == 1}" @click="setActive(1)"></a>
          </li>
          <li>
            <a href="javascript:;" :class="{'active':checked == 2}" @click="setActive(2)"></a>
          </li>
          <li>
            <a href="javascript:;" :class="{'active':checked == 3}" @click="setActive(3)"></a>
          </li>
          <li>
            <a href="javascript:;" :class="{'active':checked == 4}" @click="setActive(4)"></a>
          </li>
        </ul>
      </div>
      <div class="gray-box">
        <div class="item-box">
          <!--  -->
          <shop-item-com :key="index" v-for="(item,index) in goodsList" :item="item"></shop-item-com>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ShopItemCom from "@/components/ShopItemCom.vue";
export default {
  components: {
    ShopItemCom
  },
  data() {
    return {
      // 
      input: "",
      width: 500, //svg
      height: 500, //svg
      tagsNum: 20, //
      RADIUS: 200, //
      speedX: Math.PI / 360, //x
      speedY: Math.PI / 360, //-y
      //  https://www.jb51.net/article/168688.htm
      tags: [],
      goodsList: [],
      checked: 1,
      choose: [
        {
          label: "",
          value: 1
        },
        {
          label: "",
          value: 2
        }
      ],
      getValue: 0,
      getLabel: ""
    };
  },
  computed: {
    CX() {
      //x
      return this.width / 2;
    },
    CY() {
      //y
      return this.height / 2;
    }
  },
  //   
//   created() {
//     // //
//     // let tags = [];
//     // for (let i = 0; i < this.tagsNum; i++) {
//     //   let tag = {};
//     //   let k = -1 + (2 * (i + 1) - 1) / this.tagsNum;
//     //   let a = Math.acos(k);
//     //   let b = a * Math.sqrt(this.tagsNum * Math.PI); //

//     //   tag.x = this.CX + this.RADIUS * Math.sin(a) * Math.cos(b); //x,y,z
//     //   tag.y = this.CY + this.RADIUS * Math.sin(a) * Math.sin(b);
//     //   tag.z = this.RADIUS * Math.cos(a);
//     //   //   tag.href = thi; //
//     //   tags.push(tag);
//     // }
//     // this.tags = tags; //vue
//   },
  methods: {
      setActive(data) {
      this.checked = data;
    },
    //   
    s(data) {
      this.input = data;
      this.getValue = 2;
      this.getLabel = data
    },
    getItem(val) {
      //valselectvaluefruitEnName
      var obj = {};
      obj = this.choose.find(item=> {
        return item.value === val;
      });
      //obj label
      console.log(obj.value); //label
      console.log(val); //value
      this.getValue = val;
      this.getLabel = obj.value;
    },
    getO(){

    },
    showAllTag() {
      this.$axios({
        url: "/api/showAllTag",
        methods: "get"
      }).then(res => {
        console.log("");
        console.log(res);
        let tags = [];
        res.data.forEach((value, index) => {
            // 
          if (value == null) {
            return;
          }
          let tag = {};
          let k = -1 + (2 * (index + 1) - 1) / res.data.length;
          let a = Math.acos(k);
          let b = a * Math.sqrt(res.data.length * Math.PI); //
          tag.text = value;
          tag.x = 250 + 200 * Math.sin(a) * Math.cos(b); //x,y,z
          tag.y = 250 + 200 * Math.sin(a) * Math.sin(b);
          tag.z = 200 * Math.cos(a);
          //   tag.href = thi; //
          tags.push(tag);
        });
        // tags = Array.from(new Set(tags));
        this.tags = tags;
      });
    },
    search() {
      if (this.getValue == 1 || this.getValue == 0) {
        //   this.$message.success(this.select);
        this.$axios({
          url: "/api/goodsName",
          methods: "get",
          params: {
            name: this.input
          }
        }).then(res => {
          console.log(res);
          this.goodsList = res.data;
        });
      } else {
        this.$axios({
          url: "/api/goodsByTag",
          methods: "get",
          params: {
            goods_tag: this.getLabel
          }
        }).then(res => {
          console.log(res);
          this.goodsList = res.data;
        });
      }
    },
    rotateX(angleX) {
      var cos = Math.cos(angleX);
      var sin = Math.sin(angleX);
      for (let tag of this.tags) {
        var y1 = (tag.y - this.CY) * cos - tag.z * sin + this.CY;
        var z1 = tag.z * cos + (tag.y - this.CY) * sin;
        tag.y = y1;
        tag.z = z1;
      }
    },
    rotateY(angleY) {
      var cos = Math.cos(angleY);
      var sin = Math.sin(angleY);
      for (let tag of this.tags) {
        var x1 = (tag.x - this.CX) * cos - tag.z * sin + this.CX;
        var z1 = tag.z * cos + (tag.x - this.CX) * sin;
        tag.x = x1;
        tag.z = z1;
      }
    },
    listener(event) {
      //
      var x = event.clientX - this.CX;
      var y = event.clientY - this.CY;
      this.speedX =
        x * 0.0001 > 0
          ? Math.min(this.RADIUS * 0.00001, x * 0.0001)
          : Math.max(-this.RADIUS * 0.00001, x * 0.0001);
      this.speedY =
        y * 0.0001 > 0
          ? Math.min(this.RADIUS * 0.00001, y * 0.0001)
          : Math.max(-this.RADIUS * 0.00002, y * 0.0001);
    }
  },

  mounted() {
     //
    setInterval(() => {
      this.rotateX(this.speedX);
      this.rotateY(this.speedY);
    }, 17);
    // 
    this.showAllTag();
   
  }
};
</script>


