import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';

Vue.use(Vuex)

let store = new Vuex.Store({
  // state
  // state
  //  this.$state.state.  state
  state: sessionStorage.getItem('state') ? JSON.parse(sessionStorage.getItem('state')) : {
    goodsList: [],
    //  
    carPanelData: [],
    //  
    receiveInfo: [{
      'name': '',
      'phone': '13811111111',
      'areaCode': '010',
      'landLine': '64627856',
      'provinceId': 110000,
      'province': '',
      'cityId': 110100,
      'city': '',
      'countyId': 110106,
      'county': '',
      'add': '6319',
      'default': true,
      'checked': true
    }, {
      'name': '',
      'phone': '13811111111',
      'areaCode': '010',
      'landLine': '64627856',
      'provinceId': 110000,
      'province': '',
      'cityId': 110100,
      'city': '',
      'countyId': 110106,
      'county': '',
      'add': '6350',
      'default': false,
      'checked': false
    }],
    //  
    provisionalOrder: [],
    //  
    orderData: [],
    //  
    maxOff: false,
    //  
    carShow: false,
    //  
    isLogin: true,
    //  
    loginStatus: {},
    //  
    ball: {
      show: false,
      el: null,
      img: ''
    },
    // 
    formPage: "",
    myImage:[]
  },
  //  statestate
  getters: {
    // 
    totleCount(state) {
      let count = 0
      state.carPanelData.forEach((goods) => {
        count += goods.count;
      })
      return count
    },
    // 
    totlePrice(state) {
      let total = 0
      state.carPanelData.forEach((goods) => {
        total += goods.goods_price * goods.count
      })
      return total
    },
    // 
    checkedCount(state) {
      let count = 0
      state.carPanelData.forEach((goods) => {
        if (goods.checked)
          count += goods.count;
      })
      return count
    },
    // 
    checkedPrice(state) {
      let total = 0
      state.carPanelData.forEach((goods) => {
        if (goods.checked)
          total += goods.goods_price * goods.count
      })
      return total
    },
    // 
    checkedCarPanelData(state) {
      let checkedCarPanelData = []
      // push
      state.carPanelData.forEach((goods) => {
        if (goods.checked)
          checkedCarPanelData.push(goods);
      })
      return checkedCarPanelData
    },
    // getter
    // 
    allChecked(state) {
      let allChecked = true
      state.carPanelData.forEach((goods) => {
        // 
        if (!goods.checked) {
          allChecked = false
          return
        }
      })
      // 
      return allChecked
    },
    maxCount(state) {
      let maxOff = false
      state.carPanelData.forEach((goods) => {
        if (goods.count > goods.is_deal) {
          goods.count--
        }
      })
      return maxOff
    }
  },
  //  
  mutations: {
    // 
    addCarPanelData(state, data) {
      let bOff = true
      state.carPanelData.forEach((goods) => {
        if (goods.goods_id === data[0].goods_id) {
          goods.count += data[1]
          if (goods.count > goods.is_deal) {
            goods.count -= data[1]
            state.maxOff = true
            bOff = false
            return
          }
          state.ball.el = event.path[0]
          state.ball.show = true
          state.ball.img = data[0].goods_image
          bOff = false
          state.carShow = true
        }
      })
      if (bOff) {
        let goodsData = data[0]
        Vue.set(goodsData, 'count', data[1])
        Vue.set(goodsData, 'checked', true)
        // 
        state.carPanelData.push(goodsData)
        //  
        state.carShow = true
        state.ball.el = event.path[0]
        state.ball.show = true
        state.ball.img = data[0].goods_image
      }
    },
    plusCarPanelData(state, id) {
      state.carPanelData.forEach((goods, index) => {
        if (goods.goods_id === id) {
          if (goods.count === goods.id_deal) return
          goods.count++
          return
        }
      })
    },
    subCarPanelData(state, id) {
      state.carPanelData.forEach((goods, index) => {
        if (goods.goods_id === id) {
          if (goods.count === 1) return
          goods.count--
          return
        }
      })
    },
    // 
    delCarPanelData(state, id) {
      state.carPanelData.forEach((goods, index) => {
        if (goods.goods_id === id) {
          // id
          state.carPanelData.splice(index, 1)
          return
        }
      })
    },
    //  true = false
    checkGoods(state, id) {
      state.carPanelData.forEach((goods, index) => {
        if (goods.goods_id === id) {
          goods.checked = !goods.checked
        }
      })
    },
    // 
    allGoodsCheck(state, checked) {
      // 
      if (checked) {
        // 
        state.carPanelData.forEach((goods, index) => {
          goods.checked = false
        })
      } else {
        // 
        state.carPanelData.forEach((goods, index) => {
          goods.checked = true
        })
      }
    },
    // 
    delCheckGoods(state) {
      // 
      let i = state.carPanelData.length
      while (i--) {
        if (state.carPanelData[i].checked) {

          state.carPanelData.splice(i, 1)
        }
      }
    },
    // 
    submitOrder(state, data) {
      let i = state.carPanelData.length
      while (i--) {
        // 
        if (state.carPanelData[i].checked) {
          console.log("store");
          console.log(state.carPanelData[i])
          var id= state.carPanelData[i].goods_id
          console.log(id)
          var name= state.carPanelData[i].goods_name
          var image =  state.carPanelData[i].goods_image
          console.log(name)
          var number= state.carPanelData[i].count
          console.log(number)
          var price= state.carPanelData[i].goods_price
          console.log(price)
          // 
          setTimeout(()=>{
            axios({
              url: "/api/saveOne",
              method: 'get',
              params: {
                order_id: data.orderId,
                user_account:state.loginStatus.user_account,
                goods_id: id,
                goods_name: name,
                goods_image: image,
                goods_number: number,
                goods_price: price,
              }
            }).then(res => {
              console.log("");
              console.log(res)
            })
          },1000)
          state.carPanelData.splice(i, 1)
        }
      }
      // 
      state.orderData.unshift(data)
      // 
      state.receiveInfo.forEach((receive) => {
        receive.checked = receive.default
      })

    },
    // 
    payNow(state, id) {
      setTimeout(() => {
        state.orderData.forEach((order, index) => {
          if (order.orderId === id) {
            order.isPay = false
            // 
            axios({
              url: "/api/getPagePay",
              method: "get",
              params: {
                order_id: order.orderId,
                order_price: order.price,
                order_info: order.invoiceTitle
              }
            }).then(res => {
              console.log(1)
              console.log(res)
              console.log(2)
              console.log(res.data)
              console.log(3)
              if (res.status == 200) {
                console.log(4)
                order.isPay = true
                state.formPage = res.data.pay

                return
              } else {
                console.log(5)
                alert("")
                order.isPay = false
                return
              }

            })
            order.isPay = false
            return
          }
        })

      }, 1000)
    },
    // 
    alertPrompt(state) {
      state.maxOff = true
    },
    closePrompt(state) {
      state.maxOff = false
    },
    closeLogin(state) {
      state.isLogin = false
    },
    showCar(state) {
      state.carShow = true
    },
    hideCar(state) {
      state.carShow = false
    },
    //  state
    checkDefault(state, data) {
      state.receiveInfo.forEach((receive, index) => {
        // 
        if (receive == data) {
          receive.default = true
          // state.receiveInfo.unshift(state.receiveInfo.splice(index,1)[0])
        } else {
          receive.default = false
        }
      })
    },
    checkOut(state, data) {
      state.provisionalOrder = data
    },
    // 
    submitReceive(state, data) {
      // 
      if (data[0].default) {
        state.receiveInfo.forEach((receive, index) => {
          receive.default = false
          receive.checked = false
        })
        //      state.receiveInfo.unshift(data)
        //      return
      }
      if (data[1] == null) {
        state.receiveInfo.push(data[0])
      } else {
        console.log(data[0])
        state.receiveInfo[data[1]] = data[0]
      }
    },

  },
  actions: {

  }
})

export default store
